/**
 * @var {number} count
 */
let count = 0

/**
 * @function noop
 */
function noop() { }

/**
 *  jsonp 
 * @function jsonp
 * @param {string}                url   
 * @param {object | function}     [data] 
 * @param {object | function}     [opts] 
 * @param {function}              [fn] 
 * @returns Promise instance
 */
export default function jsonp(url, data, opts, cb) {

  if (!url || typeof url !== 'string') {
    throw Error('jsonp:  string  url ')
  }

  // 
  if (typeof data === 'function') {
    cb = data
    opts = {}
  }
  if (typeof opts === 'function') cb = opts

  if (!data) data = {}
  if (!opts) opts = {}

  /**
   * @var {string}  param      jsonp 
   * @var {string}  prefix    
   * @var {string}  id         [ prefix + count]
   * @var {number}  timeout   () 0 
   */
  const param = opts.param || 'callback',
    prefix = opts.prefix || 'jsonp',
    id = opts.name || prefix + count++,
    timeout = typeof opts.timeout === 'number' ? ~~opts.timeout : 60000

  const queryString = getQueryString(data)

  url += `${url.indexOf('?') === -1 ? '?' : '&'}${param}=${id}${queryString ? '&' + queryString : ''}`

  let timer, script

  /**
   * @function clean
   */
  function clean() {
    if (script && script.parentNode) {
      script.parentNode.removeChild(script)
    }
    window[id] = noop
    clearTimeout(timer)
  }

  /**
   * @function mount
   */
  function mount() {
    script = document.createElement('script')
    script.src = url
    document.body.appendChild(script)
  }

  mount()

  return new Promise((resolve, reject) => {
    /**
     * 
     */
    if (timeout) {
      timer = setTimeout(() => {
        clean()
        const err = new Error('jsonp: request timeout!')
        if (typeof cb === 'function') cb(err)
        reject(err)
      }, timeout)
    }

    /**
     * 
     */
    window[id] = data => {
      clean()
      if (typeof cb === 'function') cb(null, data)
      resolve(data)
    }
  })
}

/**
 * 
 * @param   {object}   data
 * @returns
 */
function getQueryString(data) {
  let queryString = ''

  const keys = Object.keys(data)

  keys.forEach(key => queryString += `&${key}=${encodeURIComponent(data[key])}`)

  return queryString ? queryString.slice(1) : ''
}


